define([
    './Logger',
    './ConsoleAppender',
    './logging.json'
], function(Logger, ConsoleAppender, logging) {
    'use strict';

    return {
        createLogger: function() {
            return new Logger();
        }, // Base logger with nothing appended
        Logger: Logger,
        ConsoleAppender: ConsoleAppender,
        level: logging.level // Object with log levels
    };
});